import { Button, TextField } from '@mui/material';
import * as React from 'react';
function AddressDtl(props) {
    const { onNextClick,onBackClick } = props;
    const [disableButton, setDisableButton] = React.useState(false);
    const [addressLineOne, setaddressLineOne] = React.useState();
    const [addressLineTwo, setaddressLineTwo] = React.useState();
    const [landMark, setlandMark] = React.useState();
    const [state, setState] = React.useState();
    const [pincode, setpincode] = React.useState();
    const onaddressLineOnevalueChange = async (value) => {
        setaddressLineOne(value.target.value)
    }
    const onaddressLineTwovalueChange = async (value) => {
        setaddressLineTwo(value.target.value)
    }
    const onlandMarkvalueChange = async (value) => {
        setlandMark(value.target.value)
    }
    const onStatevalueChange = async (value) => {
        setState(value.target.value)
    }
    const onpincodevalueChange = async (value) => {
        setpincode(value.target.value)
    }
    const onNxtClick = async () => {
        let data = {
            address:addressLineOne +","+addressLineTwo+","+landMark+","+state+","+pincode,
            index:2
        }
        onNextClick(data)
    }
    const onBckClick = async () => {
        let data = {
            index:0
        }
        onBackClick(data)
    }
    return (
        <div>
            <TextField label='Address Line1'
                value={addressLineOne}
                onChangeCapture={onaddressLineOnevalueChange}
                onChange={onaddressLineOnevalueChange}
                variant='outlined'
                size='small'
                type='text'
                style={{ display: 'flex', borderColor: '#000a00', borderRadius: 16, marginTop: '20px', marginLeft: '50px', marginRight: '50px', marginBottom: '20px' }}
                color='secondary'
            />
            <TextField label='Address Line2'
                value={addressLineTwo}
                onChangeCapture={onaddressLineTwovalueChange}
                onChange={onaddressLineTwovalueChange}
                variant='outlined'
                size='small'
                type='text'
                style={{ display: 'flex', borderColor: '#000a00', borderRadius: 16, marginTop: '20px', marginLeft: '50px', marginRight: '50px', marginBottom: '20px' }}
                color='secondary'
            />
            <TextField label='Near Landmark'
                value={landMark}
                onChangeCapture={onlandMarkvalueChange}
                onChange={onlandMarkvalueChange}
                variant='outlined'
                size='small'
                type='text'
                style={{ display: 'flex', borderColor: '#000a00', borderRadius: 16, marginTop: '20px', marginLeft: '50px', marginRight: '50px', marginBottom: '20px' }}
                color='secondary'
            />
            <TextField label='State'
                value={state}
                onChangeCapture={onStatevalueChange}
                onChange={onStatevalueChange}
                variant='outlined'
                size='small'
                type='text'
                style={{ display: 'flex', borderColor: '#000a00', borderRadius: 16, marginTop: '20px', marginLeft: '50px', marginRight: '50px', marginBottom: '20px' }}
                color='secondary'
            />
            <TextField label='Pinecode'
                value={pincode}
                onChangeCapture={onpincodevalueChange}
                onChange={onpincodevalueChange}
                variant='outlined'
                size='small'
                type='number'
                style={{ display: 'flex', borderColor: '#000a00', borderRadius: 16, marginTop: '20px', marginLeft: '50px', marginRight: '50px', marginBottom: '20px' }}
                color='secondary'
            />
            <div style={{ display: 'flex' }}>
            <Button disabled={disableButton} disableElevation style={{marginLeft: '50px',marginTop:'30px', alignItems: 'end', borderColor:'#4bcd3e',color:'#4bcd3e', textTransform: 'initial', fontWeight: 'bold' }}
                    variant='outlined' onClick={onBckClick} sx={{ height: '100%',width:'13vw' }}>
                    Back
                </Button>
                <Button disabled={disableButton} disableElevation style={{marginLeft: '50px',marginTop:'30px', alignItems: 'end', color: disableButton ? '#b3b3b3' : '#000a00', borderRadius: 8, backgroundColor: disableButton ? '#f2f2f2' : '#4bcd3e', textTransform: 'initial', fontWeight: 'bold' }}
                    variant='contained' onClick={onNxtClick} sx={{ height: '100%',width:'13vw' }}>
                    Next
                </Button>
            </div>
        </div>
    );
}
export default AddressDtl;